.banner {
	align-items: center;
	justify-content: center;
	display: flex;
	overflow: hidden;
}

.big-banner img {
	width: 100%;
}

.big-banner {
	width: 100%;
}

.small-banner {
	height: 100%;
	flex: 1 0;
	display: flex;
	flex-direction: column;
}

.small-banner-img {
	display: flex;
	height: 200px;
	flex: 1 0;
	justify-content: center;
	align-items: center;
	max-width: 100%;
}

.small-banner-img img {
	max-height: 95%;
	max-width: 100%;
}

.small-banner-text {
	flex: 0 0;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.small-banners {
	display: flex;
	flex-direction: column;
}

@media only screen and (min-width: 600px) {
	/* For tablets */
	.small-banners {
		flex-direction: row;
	}

	.small-banner {
		flex-direction: row;
	}
  }
  
  @media only screen and (min-width: 768px) {
	/* For desktop */
	.small-banner {
		flex-direction: column;
	}
  }